.label {
  display: flex;
  flex-direction: column;
  row-gap: 9px;
  width: 320px;
  &_text {
    font-size: 16px;
    font-weight: 400;
    color: #5577ff;
  }
  &_input {
    transition-duration: 0.5s;
    padding-left: 8px;
    width: 100%;
    height: 40px;
    border-radius: 10px;
    border: 1.3px solid #cccccc80;
    &:focus {
      transition-duration: 0.7s;
      outline: none;
      border: 1.3px solid #5577ff;
    }
  }
}
